.login{
height: 100vh;
background-color: rgb(193, 190, 255);
display: flex;
justify-content: center;
align-items: center;

    .card{
        width: 50%;
        display: flex;
        background-color: white;
        border-radius: 10px;
        overflow: hidden;


        .left{
            flex: 1;
            background: linear-gradient(rgba(253, 203, 118, 0.472), rgba(0, 208, 255, 0.485)), url("https://images.unsplash.com/photo-1500485035595-cbe6f645feb1?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=987&q=80") center;
            min-height: 600px;
            background-size: cover;
            padding: 50px;
            display: flex;
            flex-direction: column;
            gap: 30px;
            color: white;


            h1{
                font-size: 100px;
                line-height: 1;
            }
            p{}
            span{
                font-size: 14px;
            }
            button{
                width: 50%;
                padding: 10px;
                border: none;
                background-color: white;
                color: rebeccapurple;
                font-weight: bold;
                cursor: pointer;

            }
        }
        .right{
            flex: 1;
            padding: 50px;
            display: flex;
            flex-direction: column;
            gap: 50px;
            justify-content: center;
            h1{
                color: #555;
            }
            form{
                display: flex;
                flex-direction: column;
                gap:  30px;
                input{
                    border: none;
                    border-bottom: 1px solid lightgray;
                    padding: 20px 10px;
                }
                button{
                    width: 50%;
                    padding: 10px;
                    border: none;
                    background-color: #938eef;
                    color: white;
                    font-weight: bold;
                    cursor: pointer;

                }
            }
        }
    }
}