@import "../../style.scss";

.profile{
    @include themify($themes){
        background-color: themed("bgSoft");

        .images{

            width: 100%;
            height: 300px;
            position: relative;

            .cover{
                height: 100%;
                width: 100%;
                object-fit: cover;

            }
            
            .profilePic{
                width: 200px;
                height: 200px;
                border-radius: 50%;
                object-fit: cover;
                position: absolute;
                left: 0;
                right: 0;
                margin: auto;
                top: 200px;
            }
        }

        .profileContainer{
            padding: 20px 70px;

            @include mobile{
               padding: 10px;
             }

            @include tablet{
               padding: 20px;
             }

            .uInfo{
            height: 180px;
            box-shadow: -5px 5px 21px 6px rgba(0,0,0,0.2);
            border-radius: 20px;
            background-color: themed("bg");
            color: themed("textColor");
            padding: 150px 50px 100px 50px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;

            @include mobile{
                flex-direction: column;
                height: 30vh;
                padding: 20px;
                margin-top: 100px;
             }

            .left{
                flex:1;
                display: flex;
                gap: 10px;

                @include tablet{
                   flex-wrap: wrap;
                }

                a{
                    color: themed("textColorSoft")
                }
            }
            .center{
                flex:1;
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 10px;

                span{
                    font-size: 30px;
                    font-weight: 500;
                }

                .info{
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: space-around;

                    .item{
                        display: flex;
                        align-items: center;
                        gap: 5px;
                        color: themed("textColorSoft")
                        
                    
                    }

                }
                button{
                    border: none;
                    background-color: #5271ff;
                    color: white;
                    padding: 10px 20px;
                    border-radius: 5px;
                    cursor: pointer;
                }
            }
            .right{
                flex:1;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                gap: 10px;
            }
        }}
    }
}